<template>
  <div class="record mt-5">
    <div class="content p-5 mx-auto">
      <div class="d-flex justify-content-center">
        <div class="p-2 left-btn" @click="frame = 'exchange'" :class="{'btn-checked':frame === 'exchange'}"></div>
        <div class="p-2 right-btn" @click="frame = 'history'" :class="{'btn-checked':frame === 'history'}"></div>
      </div>
      <div v-if="frame === 'exchange'" class="p-2 border">
        exchangeexchange
        <button @click="usecoupon()"></button>
      </div>
      <div v-if="frame === 'history'" class="p-2 border">
        xu3historyhistory
      </div>
    </div>
    <div v-if="modalframe" class="frame-modal d-flex align-items-center justify-content-center">
      <div class="p-5 bg-white border rounded">
        <p @click="modalframe = false">close</p>
        <vue-qr class="erweima" :text="linkurl"
          :size="200"></vue-qr>
        <!-- <img src="../assets/images/qrcode.png" width="200px" alt="" class="border"> -->
      </div>
    </div>
  </div>
</template>

<script>
import VueQr from 'vue-qr'
// import QRCode from 'qrcodejs2'
export default {
  name: 'record',
  data () {
    return {
      frame: 'exchange',
      modalframe: false,
      linkurl: 'https://pjchender.blogspot.com/2015/05/rwd.html'
    }
  },
  components: {
    VueQr
  },
  methods: {
    usecoupon () {
      this.modalframe = true
      this.linkurl = 'https://uupon.tw/'
    }
  }
}
</script>

<style>
.record .content{
  min-height: calc( 100vh - 221px);
}
</style>



